import Vue from 'vue'
import Router from 'vue-router'
import Signlogin from './components/Signlogin.vue'
import register from './components/register.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Signlogin',
      component: Signlogin
    },
    {
      path: '/register',
      name: 'register',
      component: register
    }
  ]
})
