<template>
  <div class=container>
    <div class="interface">
      <el-form label-position="right" label-width="60px">
        <el-form-item>
          <el-input style=" width: 250px; " placeholder="请输入账号" v-model="username" @change="checkUsername()" clearable>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input style=" width: 250px; " type="password" placeholder="请输入密码" @change="checkPassword()"
            v-model="password" clearable>
          </el-input>
        </el-form-item>
      </el-form>
      <el-button style="color:＃409EFF" @click="login" class="loginbutton1" plain>登陆</el-button>
      <el-button style="color:＃409EFF" @click="goRegister" plain>注册</el-button>
    </div>
  </div>
</template>
<script>
export default {
  created () {
    document.title = 'iotat-登陆'
  },
  data () {
    return {
      username: '',
      password: '',
      minlength: '6',
      maxlength: '20',
      mark: false
    }
  },
  methods:
  {
    login () {
      var that = this
      if (this.mark == true) {
        that.$message({
          showClose: true,
          message: '请检查账号和密码的输入',
          type: 'warning'
        });
      }
      else {        this.$axios({
          method: 'post',
          url: 'http://10.10.5.130:18887/login?userName=' + that.username + '&password=' + that.password
        }).then(res => {
          console.log(res)
          if (res.data.status == 200) {
            that.$alert('登陆成功', { confirmButtonText: '确定' })

          }
          else {
            that.$alert('登陆失败', { confirmButtonText: '确定' })
          }
        })      }

    },
    goRegister () {
      this.$router.push("/register")
    },
    checkUsername () {
      var that = this
      this.mark = false
      if (this.username.length > that.maxlength || this.username.length < that.minlength) {
        this.mark = true
        that.$message({
          showClose: true,
          message: '用户名输入不合法',
          type: 'warning'
        });
      }
    },
    checkPassword () {
      var that = this
      this.mark = false
      if (this.password.length > that.maxlength || this.password.length < that.minlength) {
        this.mark = true
        that.$message({
          showClose: true,
          message: '密码输入不合法',
          type: 'warning'
        });
      }

    }

  }
}

</script>
<style scoped>
@font-face {
  font-family: "iconfont";
  src: url("../assets/iconfont/iconfont.eot");
  src: url("../assets/iconfont/iconfont.eot?#iefix") format("embedded-opentype"),
    url("../assets/iconfont/iconfont.woff2") format("woff2"),
    url("../assets/iconfont/iconfont.woff") format("woff"),
    url("../assets/iconfont/iconfont.ttf") format("truetype"),
    url("../assets/iconfont/iconfont.svg#iconfont") format("svg");
}
.interface {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -211px;
  margin-top: -107.8px;
  border-radius: 5px;
  /* margin: auto; */
  width: 350px;
  padding: 35px 35px 15px;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  background: url(../assets/img/1.png);
  background-size: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  -webkit-font-smoothing: antialiased;
}
.loginbutton1 {
  margin-left: 100px;
}
</style>