<template>
  <div class=container>
    <div class="interface">
      <el-form label-position="right" label-width="60px">
        <el-form-item>
          <el-input style=" width: 250px; " v-model="userName" placeholder='请输入6-20位的用户名' @change="checkUsername()"
            clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-input style="width: 250px;" type="password" v-model="passWord" placeholder='请输入6-20位的密码'
            @change="checkPassword()" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-input style="width: 250px;" type="password" v-model="rePassWord" placeholder='请输入6-20位的密码'
            @change="checkRepassword()" clearable>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input style="width: 250px;" v-model="realName" placeholder='请输入您的真实姓名' @change="checkRealName()"
            clearable>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input style="width: 250px;" v-model="stuId" placeholder='请输入您的学号' @change="checkStuid()" clearable>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input style="width: 250px;" v-model="userMail" placeholder='请输入您的邮箱' @change="checkUserMail()" clearable>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input style="width: 250px;" v-model="Mac" placeholder='请输入您的mac地址' @change="checkMac()" clearable>
          </el-input>
        </el-form-item>

      </el-form>
      <el-button class="loginbutton2" style="color:＃409EFF" @click="gologin" plain>登陆</el-button>
      <el-button style="color:＃409EFF" @click="register" plain>注册</el-button>
    </div>
  </div>
</template>

<script>
export default {
  created () {
    document.title = 'iotat-注册'
  },
  data () {
    return {
      userName: '',
      passWord: '',
      rePassWord: '',
      realName: '',
      stuId: '',
      userMail: '',
      Mac: '',
      minlength: '6',
      maxlength: '20',
      mark: false

    }
  },
  methods: {

    register () {
      var that = this
      if (this.mark == true) {
        this.$message({
          showClose: true,
          message: '请检查输入',
          type: 'warning'
        });
      }
      else {
        this.$axios({
          method: 'post',
          url: 'http://10.10.5.130:18887/register?userName=' + that.userName + '&password=' + that.passWord + '&mac=' + that.Mac + '&realName=' + that.realName + '&stuId=' + that.stuId + '&userMail=' + that.userMail
        }).then(res => {
          console.log(res)
          if (res.data.status == 200) {
            that.$alert('注册成功', { confirmButtonText: '确定' })

          }
          else if (res.data.status == 403) {
            that.$alert('用户存在', { confirmButtonText: '确定' })
          }
          else {
            that.$alert('注册失败', { confirmButtonText: '确定' })
          }
        })      }
    },
    checkUsername () {
      var that = this
      this.mark = false
      if (this.userName.length > that.maxlength || this.userName.length < that.minlength) {
        this.mark = true
        this.$message({
          showClose: true,
          message: '账号输入不合法',
          type: 'warning'
        });
      }

    },
    checkPassword () {
      var that = this
      this.mark = false
      if (this.passWord.length > that.maxlength || this.passWord.length < that.minlength) {
        this.mark = true
        this.$message({
          showClose: true,
          message: '密码输入不合法',
          type: 'warning'
        });
      }
    },
    checkRepassword () {
      var that = this
      this.mark = false
      if (this.rePassWord.length > that.maxlength || this.rePassWord.length < that.minlength) {
        this.mark = true
        this.$message({
          showClose: true,
          message: '密码输入不合法',
          type: 'warning'
        });
      }
      if (this.rePassWord != this.passWord) {
        this.mark = true
        this.$message({
          showClose: true,
          message: '密码输入不一致',
          type: 'warning'
        });
      }
    },
    checkRealName () {
      this.mark = false
      var realname = /^[\u4E00-\u9FA5]{2,4}$/
      if (!realname.test(this.realName)) {
        this.mark = true
        this.$message({
          showClose: true,
          message: '真实姓名输入不合法',
          type: 'warning'
        });
      }

    },
    checkStuid () {
      this.mark = false
      if (this.stuId.length != 10) {
        this.mark = true
        this.$message({
          showClose: true,
          message: '学号输入不合法',
          type: 'warning'
        });
      }
    },
    checkUserMail () {
      this.mark = false
      var usermail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (!usermail.test(this.userMail)) {
        this.mark = true
        this.$message({
          showClose: true,
          message: '邮箱输入不合法',
          type: 'warning'
        });
      }
    },
    checkMac () {
      this.mark = false
      var mac = /^([A-Fa-f0-9]{2}-){5}[A-Fa-f0-9]{2}$/
      if (!mac.test(this.Mac)) {
        this.mark = true
        this.$message({
          showClose: true,
          message: 'MAC地址输入不合法',
          type: 'warning'
        });
      }

    },
    gologin () {
      this.$router.push("/")
    }
  }



}
</script>

<style scoped>
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  background: url(../assets/img/1.png);
  background-size: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  -webkit-font-smoothing: antialiased;
}
.interface {
  /* position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -211px;
  margin-top: -107.8px; */
  border-radius: 5px;
  margin: auto;
  width: 350px;
  padding: 35px 35px 15px;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.loginbutton2 {
  margin-left: 100px;
}
</style>